import lang_pack_total from "../lang/lang"

var global = {
  "lang": "zh",
  "lang_pack": {},
  "width": 1920,// current width
  "height": 1080, // current height
  "mobile_bound": 540,// the bound of the mobile device
  "mode":0,//0 for pc device, 1 for mobile device
  "menu_on": false, // if menu is opened
  refresh_windows_size(){
    this.width = document.documentElement.clientWidth || document.body.clientWidth
    this.height = document.documentElement.clientHeight || document.body.clientHeight
    // change the mode and the root size of the device
    if(this.width <= this.mobile_bound){
      this.mode = 1
      document.documentElement.style.fontSize = window.innerWidth / 750 + "px";
    }else{
      this.mode = 0
      document.documentElement.style.fontSize = window.outerWidth / 1920 + "px";
    }
  },
  set_lang_pack() {
    global.lang = lang_pack_total.default_lang;
    this.load_language()
    switch (global.lang) {
      case "en":
        global.lang_pack = lang_pack_total.lang_pack_en
        break
      case "cn":
        global.lang_pack = lang_pack_total.lang_pack_cn
        break
      case "tc":
        global.lang_pack = lang_pack_total.lang_pack_tc
        break
    }
    // ----------------------------
    // global.lang = "en"
  },
  change_lang() {
    // var lang = localStorage.getItem("lang");
    localStorage.setItem("lang", global.lang);
    this.set_lang_pack()
  },
  load_language() {
    var lang = localStorage.getItem("lang");
    if (lang == null) {
      lang = navigator.language;
      if (lang.slice(0, 2) === "zh") {
        if (lang === "zh-CN") global.lang = "cn";
        else global.lang = "tc";
      } else {
        global.lang = "en";
      }
      localStorage.setItem("lang", global.lang);
    } else {
      global.lang = lang;
    }
    // test only english-----------
  }
}
export default global



