<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

div.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  height: 80px;
  width: 100%;
  background: #f7f7f7;
}

div.navigation_bar{
  position: fixed;
  z-index: 2;
  right: 11.98%;
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  top: 40px;
  height: 25px;
  flex-wrap: wrap;
}

div.separator{
  height: 28px;
  width: 3px;
  transform:scale(0.5 , 1);
  background: #37383d;
}

div.navigation_text{
  font-size: 18px;
  line-height: 25px;
  padding-left: 7px;
  padding-right: 7px;
}
div.navigation_text:hover{
  /*color: #efcd24;*/
  border-bottom: #efcd24 solid 3px;
  cursor: pointer;

}

div.logo_box {
  position: fixed;
  z-index: 100;
  top: 40px;
  left: 11.98%;
  background: transparent;
  cursor: pointer;
}

path.logo-1 {
  fill: #251714;
}

path.logo-2 {
  fill: #efcd24;
}

svg.logo_svg {
  width: 220px;
}


/**/
@media screen and (max-width: 540px) {
  div.header {
    position: fixed;
    top: 0;
    z-index: 2;
    height: 120rem;
    width: 100%;
    background: #f7f7f7;
  }

  div.logo_box {
    top: 50rem;
    left: 25rem;
  }

  svg.logo_svg {
    width: 260rem;
  }
}
</style>


<script>
import anime from "../../../modules/anime";
import global from "../../pages/global";

let header_move = false

let vm = {
  name: "Logo",
  props:{
    canBeHidden: true
  },
  data() {
    return {
      global:global,
      show: true
    };
  },
  beforeMount() {

  },
  mounted() {
    if(document.getElementById("home_page"))
      this.show = false
    else
      this.show = true

    window.addEventListener('wheel', this.handleMenuScroll);

  },
  destroyed() {
    window.removeEventListener('wheel', this.handleMenuScroll);
  },
  methods: {
    to_home() {
      let that = this;
      if (that.$parent.is_show_menu) {
        that.$parent.click_arrow();
        setTimeout(function () {
          that.$router.push("/");
        }, 1167);
      } else {
        that.$router.push("/")
      }
    },
    change_color(signch_ck, e, dur) {
      anime({
        targets: "path.logo-1",
        fill: signch_ck,
        duration: dur,
        easing: "linear",
      });

      anime({
        targets: "path.logo-2",
        fill: e,
        duration: dur,
        easing: "linear",
      });
    },
    to_stories() {
        this.$router.push("/Stories");
    },
    to_about() {
      this.$router.push("/About");
    },
    to_works() {
      this.$router.push("/Works");
    },
    to_past() {
      this.$router.push("/Past");
    },
    to_team() {
      this.$router.push("/Team");
    },
    to_contact() {
      this.$router.push("/Contact");
    },
    handleMenuScroll(e) {
      if(this.canBeHidden === false)
        return
      if(global.mode === 1)
        return;
      let that = this
      if (e.deltaY > 0){
        that.hide_header()
      }else if(e.deltaY < 0){
        that.show_header()
      }
    },
    hide_header(){
      if(header_move === true)
        return
      header_move = true
      new anime({
        targets: [document.getElementById("navigation_bar"), document.getElementById("logo_box")],
        top: -40,
        duration: 300,
        delay: 100,
        easing: 'linear',
        complete: function (){
          header_move = false
        }
      });

      new anime({
        targets: document.getElementById("header"),
        top: -80,
        duration: 300,
        delay: 100,
        easing: 'linear',
      });
    },

    show_header(){
      if(header_move === true)
        return
      header_move = true
      new anime({
        targets: [document.getElementById("navigation_bar"), document.getElementById("logo_box")],
        top: 40,
        duration: 300,
        delay: 100,
        easing: 'linear',
        complete: function (){
          header_move = false
        }
      });

      new anime({
        targets: document.getElementById("header"),
        top: 0,
        duration: 300,
        delay: 100,
        easing: 'linear',
      });
    }
  },
};

export default vm;
</script>



