<script>
import global from "../../pages/global";
import anime from "../../../modules/anime";
import Logo from "../../components/logo/Logo";
import LangTransfer from "../lang_transfer/LangTransfer";

let menu_roll_down = false;
let menu_roll_up = false;
let menu_roll_event;
let menu_change;

export default {
  name: "Menu",
  data() {
    return {
      // mark is the menu shown
      is_show_menu: false,
      // import global data
      global: global,
      // mark which menu item is focused
      menu_focus: 0,
      // mark curent rotate
      menu_rotate: 0,
    };
  },
  components: {
    Logo,
  },
  beforeMount() {
  },
  mounted() {
    global.set_lang_pack();
    global.refresh_windows_size();
    //initial the clock circle size
    this.change_menu_clock_circle();
    // listen the size of window to change the base size
    window.onresize = () => {
      return (() => {
        global.refresh_windows_size();
        this.refresh_menu_size();
      })();
    };
    document.getElementById("Menu").addEventListener('wheel', this.handleMenuScroll);
  },
  watch: {},
  destroyed() {

  },
  methods: {
    // refresh the menu size
    refresh_menu_size() {
      if (this.is_show_menu) {
        this.change_menu_clock_circle();
        this.show_menu();
      }
    },
    // show_menu
    click_arrow() {
      if (this.is_show_menu) {
        Logo.methods.change_color("#251714", "#efcd24", 1000);
        this.is_show_menu = false;
        this.hide_menu();
        if (global.mode === 0) {
          this.hide_menu_clock();
          this.menu_rotate = 0;
          this.clear_menu_focus();
          clearInterval(menu_roll_event);
          clearInterval(menu_change);
          LangTransfer.methods.hide_lang_trans()
        } else {
          this.hide_menu_seq()
        }
      } else {
        Logo.methods.change_color("#251714", "#ffffff", 1000);
        this.show_menu();
        if (global.mode === 0) {
          this.show_menu_clock();
          this.create_menu_change();
          setTimeout(() => {
            LangTransfer.methods.show_lang_trans()
          }, 500)
        } else {
          this.show_menu_seq();
        }
        let that = this;
        setTimeout(function () {
          that.is_show_menu = true;
        }, 1167);
      }
    },
    show_menu() {
      // pointer-events: fill;
      document.getElementById("ball").style.pointerEvents = "none";
      // 
      let scale_size
      if (global.mode === 0) {
        scale_size = Math.sqrt(
            Math.pow(global.width, 2) +
            Math.pow(global.height, 2)
          ) / 40
      } else {
        scale_size = Math.sqrt(
            Math.pow(global.width, 2) +
            Math.pow(global.height, 2)
          ) /
          ((global.width / 750) * 40)
      }

      new anime({
        targets: "div.ball_back",
        scale: scale_size,
        easing: "easeInOutExpo",
        duration: 1000,
      });

      new anime({
        targets: "div.ball",
        backgroundColor: "#ffffff",
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "svg.menu_arrow",
        rotate: 180,
        easing: "linear",
        duration: 500,
      });
    },

    hide_menu() {
      new anime({
        delay: 500,
        targets: "div.ball_back",
        scale: 1,
        easing: "easeInOutExpo",
        duration: 1000,
      });

      new anime({
        delay: 500,
        targets: "div.ball",
        backgroundColor: "#efcd24",
        easing: "linear",
        duration: 500,
      });

      new anime({
        delay: 500,
        targets: "svg.menu_arrow",
        rotate: 0,
        easing: "linear",
        duration: 500,
        complete: function (anim) {
          document.getElementById("ball").style.pointerEvents = "fill";
        },
      });
    },

    show_menu_clock() {
      let that = this;
      new anime({
        delay: 500,
        targets: "div.menu_clock_circle",
        rotate: [60, 0],
        easing: "linear",
        duration: 667,
        change: function (anim) {
          document.getElementById("menu_clock_circle").style.visibility =
            "visible";
        },
        complete: function (anim) {
          document.getElementById("ball").style.pointerEvents = "fill";
          that.init_menu_focus();
        },
      });

      new anime({
        delay: 500,
        targets: "div.menu_clock_arrow_box",
        rotate: [60, 0],
        easing: "linear",
        duration: 667,
        change: function (anim) {
          document.getElementById("menu_clock_arrow_box").style.visibility =
            "visible";
        },
      });
    },

    change_menu_clock_circle() {
      if (global.mode !== 0)
        return
      let radius = Math.max(global.width, global.height)
      document.documentElement.style.setProperty(
        "--circle_diameters",
        radius + "px"
      );
    },
    hide_menu_clock() {
      let that = this;
      document.getElementById("ball").style.pointerEvents = "none";
      new anime({
        targets: "div.menu_clock_circle",
        rotate: [this.menu_rotate, 90],
        easing: "linear",
        duration: 1000,
        complete: function (anim) {
          document.getElementById("menu_clock_circle").style.visibility =
            "hidden";
          that.clear_menu_focus();
        },
      });

      new anime({
        targets: "div.menu_clock_arrow_box",
        rotate: [0, 90],
        easing: "linear",
        duration: 1000,
        complete: function (anim) {
          document.getElementById("menu_clock_arrow_box").style.visibility =
            "hidden";
        },
      });
    },
    rotate_menu(event) {
      if (this.is_show_menu === false) return;
      let that = this
      let cury = event.y;
      let bound = 100;
      let rem = (document.documentElement.clientWidth || document.body.clientWidth) / 1920;
      let height = document.documentElement.clientHeight || document.body.clientHeight;
      // if mouse in select menu don't change menu rotate
      let bound_up = height / 2 + rem * bound;
      let bound_down = height / 2 - rem * bound;
      // if mouse in top part
      if (cury <= bound_down) {
        if (menu_roll_up === true) {
          menu_roll_up = false;
        }
        if (menu_roll_down === true) {
          return;
        }
        menu_roll_down = true;
        clearInterval(menu_roll_event);

        if(event.once){
          that.roll_down()
          setTimeout(()=>{
            menu_roll_down = false
          }, 1000)
        }else{
          menu_roll_event = setInterval(() => {
            that.roll_down()
          }, 500);
        }
      }

      // if mouse in down part
      if (cury >= bound_up) {
        if (menu_roll_down === true) {
          menu_roll_down = false;
        }
        if (menu_roll_up === true) {
          return;
        }
        menu_roll_up = true;
        clearInterval(menu_roll_event);
        if(event.once){
          that.roll_up()
          setTimeout(()=>{
            menu_roll_up = false
          }, 1000)
        }else{
          menu_roll_event = setInterval(() => {
            that.roll_up()
          }, 500);
        }
      }

      if (cury > bound_down && cury < bound_up) {
        menu_roll_down = false;
        menu_roll_up = false;
        clearInterval(menu_roll_event);
      }
    },

    create_menu_change() {
      let last_menu_focus = 0;
      menu_change = setInterval(() => {
        switch (this.menu_rotate) {
          case 0:
            this.menu_focus = 0;
            break;
          case -22.5:
            this.menu_focus = 1;
            break;
          case -45:
            this.menu_focus = 2;
            break;
          case -67.5:
            this.menu_focus = 3;
            break;
          case -90:
            this.menu_focus = 4;
            break;
          case -112.5:
            this.menu_focus = 5;
            break;
        }
        let roll_state = this.menu_focus - last_menu_focus;
        if (roll_state !== 0) {
          switch (this.menu_focus) {
            case 0:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
              }
              break;
            case 1:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
            case 2:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
            case 3:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
            case 4:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
            case 5:
              if (roll_state < 0) {
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
          }
        }
        last_menu_focus = this.menu_focus;
      }, 100);
    },

    roll_up(){
      // console.log(this.menu_rotate)
      if (this.menu_rotate - 22.5 < -112.5) {
        this.menu_rotate = -112.5;
        // return
      } else {
        this.menu_rotate -= 22.5;
        new anime({
          targets: "div.menu_clock_circle",
          rotate: [this.menu_rotate + 22.5, this.menu_rotate],
          easing: "linear",
          duration: 500,
        });
      }
    },

    roll_down(){
      // console.log(this.menu_rotate)
      if (this.menu_rotate + 22.5 > 0) {
        this.menu_rotate = 0;
        // return
      } else {
        this.menu_rotate += 22.5;
        new anime({
          targets: "div.menu_clock_circle",
          rotate: [this.menu_rotate - 22.5, this.menu_rotate],
          easing: "linear",
          duration: 500,
        });
      }
      // document.getElementById("menu_clock_circle").style.transform =
      //   "rotate(" + this.menu_rotate + "deg)";
    },

    lose_focus(n) {
      new anime({
        targets: ["div.menu_text_top" + n, "div.menu_bottom" + n],
        opacity: ["100%", "0%"],
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "div.menu_num" + n,
        opacity: ["100%", "50%"],
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "div.clock_dot" + n,
        backgroundColor: "#f8e791",
        easing: "linear",
        scale: 0.7,
        duration: 500,
      });
    },

    get_focus(n) {
      this.menu_focus = n;
      new anime({
        targets: ["div.menu_text_top" + n, "div.menu_bottom" + n],
        opacity: ["0%", "100%"],
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "div.menu_num" + n,
        opacity: ["50%", "100%"],
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "div.clock_dot" + n,
        backgroundColor: "#ffffff",
        easing: "linear",
        scale: 1,
        duration: 500,
      });
    },
    init_menu_focus() {
      this.lose_focus(1);
      this.lose_focus(2);
      this.lose_focus(3);
      this.lose_focus(4);
      this.lose_focus(5);
    },
    clear_menu_focus() {
      if (this.menu_focus !== 0) this.get_focus(0);
      if (this.menu_focus !== 1) this.get_focus(1);
      if (this.menu_focus !== 2) this.get_focus(2);
      if (this.menu_focus !== 3) this.get_focus(3);
      if (this.menu_focus !== 4) this.get_focus(4);
      if (this.menu_focus !== 5) this.get_focus(5);
    },

    show_menu_seq() {
      document.getElementById("ball").style.pointerEvents = "none";
      document.getElementById("seq_select_box").style.visibility = "visible"
      setTimeout(() => {
        LangTransfer.methods.show_lang_trans()
      }, 500)
      // 500 + 667
      new anime({
        targets: 'div.seq_select_box .seq_select_text',
        translateX: ["750rem", "0rem"],
        delay: function (el, i) {
          return i * 100 + 367;
        },
        duration: 300,
        easing: 'easeInOutSine',
        complete: function (anim) {
          document.getElementById("ball").style.pointerEvents = "fill";
        },
      });
    },
    hide_menu_seq() {
      document.getElementById("ball").style.pointerEvents = "none";
      LangTransfer.methods.hide_lang_trans()
      // total 500ms ~ 1000ms
      new anime({
        targets: 'div.seq_select_box .seq_select_text',
        translateX: ["0rem", "750rem"],
        delay: function (el, i) {
          return (5 - i) * 100 + 267;
        },
        duration: 300,
        easing: 'easeInOutSine',
        complete: function (anim) {
          document.getElementById("seq_select_box").style.visibility = "hidden"
          document.getElementById("ball").style.pointerEvents = "fill";
        },
      });
    },

    change_logo_color(signch_ck, e, dur) {
      Logo.methods.change_color(signch_ck, e, dur);
    },
    to_stories() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Stories");
      }, 1500);
    },
    to_about() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/About");
      }, 1500);
    },
    to_works() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Works");
      }, 1500);
    },
    to_past() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Past");
      }, 1500);
    },
    to_team() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Team");
      }, 1500);
    },
    to_contact() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Contact");
      }, 1500);
    },
    handleMenuScroll(e) {
      // let that = this
      // if(menu_roll_up === true || menu_roll_down === true)
      //   return
      // if (e.deltaY > 0){
      //   console.log("")
      //   // that.roll_up()
      //   this.rotate_menu({y:100000, once: 1})
      // }else if(e.deltaY < 0){
      //   console.log("")
      //   // that.roll_down()
      //   this.rotate_menu({y:-100000 , once: 1})
      // }
    },
  },
};
</script>



