<template>
  <div id="lang_transfer_box" class="lang_transfer_box">
    <div :style="global.lang==='en'?'color: #251714;':''" class="eng" @click="change_lang('en')">Eng</div>
    <div class="separator"></div>
    <div :style="global.lang==='tc'?'color: #251714;':''" class="tc" @click="change_lang('tc')"></div>
    <div class="separator"></div>
    <div :style="global.lang==='cn'?'color: #251714;':''" class="cn" @click="change_lang('cn')"></div>
  </div>
</template>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.lang_transfer_box {
  visibility: hidden;
  position: fixed;
  width: 11.98%;
  z-index: 100;
  top: 40px;
  right: 0;
  font-size: 20px;
  line-height: 25px;
  background: transparent;
  display: flex;
  justify-content: center;
}

div.separator {
  width:3px;
  transform:scale(0.5 , 1);
  height: 25px;
  background: #251714;
  opacity: 70%;
}

div.eng, div.tc, div.cn {
  color: #898989;
  cursor: pointer;
  padding-left: 5rem;
  padding-right: 5rem;
  padding-bottom: 1rem;
}

div.eng:hover, div.tc:hover, div.cn:hover {
  color: #ffffff;
}

/**/
@media screen and (max-width: 540px) {
  div.lang_transfer_box {
    visibility: hidden;
    z-index: 100;
    top: unset;
    bottom: -30rem;
    right: unset;
    left: 375rem;
    font-size: 40rem;
    transform: translateX(-50%);
  }

  div.separator {
    height: 40rem;
    width:3px;
    transform:scale(0.5 , 1);
    background: #251714;
    opacity: 70%;
  }
}
</style>


<script>
import global from "../../pages/global";
import anime from "../../../modules/anime";

let vm = {
  name: "LangTransfer",
  data() {
    return {
      global: global,
    };
  },
  beforeMount() {
  },
  mounted() {
  },
  destroyed() {
  },
  methods: {
    change_lang(lang) {
      global.lang = lang
      global.change_lang()
    },

    show_lang_trans() {
      if(document.getElementById("lang_transfer_box") === null)
        return
      document.getElementById("lang_transfer_box").style.visibility = "visible"
    if(global.mode === 0){
      new anime({
        targets: 'div.lang_transfer_box',
        top: "40px",
        duration: 300,
        easing: 'easeInOutSine',
      });
    }
    else if(global.mode === 1){
        new anime({
          targets: 'div.lang_transfer_box',
          bottom: "30rem",
          duration: 300,
          easing: 'easeInOutSine',
        });
      }

    },

    hide_lang_trans() {
      if(document.getElementById("lang_transfer_box") === null)
        return


      if(global.mode === 0){
        new anime({
          targets: 'div.lang_transfer_box',
          top: "-40px",
          duration: 300,
          easing: 'easeInOutSine',
          complete: function (anim) {
            document.getElementById("lang_transfer_box").style.visibility = "hidden"
          }
        });
      }
      else if(global.mode === 1){
        new anime({
          targets: 'div.lang_transfer_box',
          bottom: "-30rem",
          duration: 300,
          easing: 'easeInOutSine',
          complete: function (anim) {
            document.getElementById("lang_transfer_box").style.visibility = "hidden"
          }
        });
      }
    }
  },
};

export default vm;
</script>



